.cardContainer {
    display: inline-block;
    flex: 0 0 18%;
    border: 1.9px solid #262c3a;
    padding: 8px;
    margin: 20px;
    border-radius: 0.8em;
    background-color: rgba(7, 24, 73, 0.3);
}

.botonClose {
    text-align: center;
    background-color:bisque;
    color:#f7351b;
    font-weight: 700;
    float: right;
    border-radius: 8px;
    font-size: small;
    border: 0.5px solid #f7351b;
    transition-delay: 0.015s;
}

.botonClose:hover {
    transform: scale(1.15);
    color: bisque;
    background-color:#f7351b;
    border: 0.5px solid bisque;
}

#imagen {
    display: flex;
    border-radius: 0.8em;
    border:1px solid #262c3a;
    width: 100%;
    height: auto;
    margin-top: 2px;
    transition: transform 0.45s ease;    
}

#imagen:hover {
    transform: scale(1.01);
}

#id {
    float: left;
    font-size:0.7em;
    color: #47f667;
    font-family: Chakra Petch;
    padding: 0.5 em;
    text-shadow: 1px 1px 2px rgb(24, 22, 22), 0 0 1em rgb(226, 218, 216), 0 0 0.2em rgb(248, 248, 248);
}

#nameChar{
    font-size:0.88em;
    color: #47f667;
    font-family: Chakra Petch;
    padding: 0.6em; 
    text-shadow: 1px 1px 2px rgb(24, 22, 22), 0 0 1em rgb(226, 218, 216), 0 0 0.2em rgb(248, 248, 248);
