  html {
    font-size: 16px; /* Establece el tamaño de fuente base */
  }
  
  .navBar {
    display: flex;
    align-items: center;
    width: 100%;
    height: 10rem;
    background-image: url("C:\Users\Florencia\OneDrive\Escritorio\03fdb791c0c8753db54348da091ba79b.jpg");
    background-repeat: no-repeat;
    background-position: 100% -100%;
    background-size: cover;
  }
  
  .navBar h1 {
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: Chakra Petch;
    color:  #fefefd;
    margin-top: 1rem; /* Unidad de margen vertical relativa */
    margin-left: 4rem; /* Unidad de margen horizontal relativa */
    font-size: 2.1rem; /* Unidad de tamaño de fuente relativa */
    width: 25%;
    height: auto;
    padding-top: 0.7em;
  }
  
  #input {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 3rem; /* Unidad de margen horizontal relativa */
    width: 10rem; /* Unidad de ancho relativa */
    height: 1.5rem; /* Unidad de altura relativa */
    border: 0.2rem solid rgb(96, 231, 110); /* Unidad de borde relativa */
    border-radius: 8px; /* Unidad de borde relativa */
  }
  
  #buttonSearch {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 0.5rem; /* Unidad de margen horizontal relativa */
    height: 1.5rem; /* Unidad de altura relativa */
    width: 5rem; /* Unidad de ancho relativa */
    font-family: Chakra Petch;
    font-size: 1rem; /* Unidad de tamaño de fuente relativa */
    text-align: center;
    background-color:bisque;
    color:#f7351b;
    font-weight: 600;
    border-radius: 8px;
    border: 0.5px solid #f7351b;
    transition-delay: 0.015s;
  }

  #buttonSearch:hover {
    transform: scale(1.05);
    color: bisque;
    background-color:#f7351b;
    border: 0.5px solid bisque;
}